body{
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: #daefd9;
}
  
  .Pages{
    height: 90vh;
    width: 100vw;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    color: white;
    overflow: hidden
  }

.TimeBtn{
    text-align: center;
    height: 100vh;
    width: 100vw;
    position: absolute;
    height: 31.5%;
    width: 60%;
    left: 50%;
    margin-top:200px;
    -webkit-transform: translate(-50%, 50%);
        -ms-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);

  }
  .Xlogo-div{
    text-align: center;
    height: 100vh;
    width: 100vw;
    left: 50%;
    width: 50%;
    height: 22%;
    -webkit-transform: translate(-50%, 50%);
        -ms-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    position: absolute;
  }

  .selection-buttons .btn-info:hover, .btn-info:focus, .btn btn-success btn-lg btn-block:active, .btn-info.active, .open .dropdown-toggle.btn-info {
    color: #FFF;
    background-color: #00CC66;
    border-color: #fff; /* 00b75b */ 
  }


